@mixin breakpoint($point) {
  @if $point ==lg {
    @media (max-width: 992px) {
      @content;
    }
  } @else if $point ==md {
    @media (max-width: 768px) {
      @content;
    }
  } @else if $point ==sm {
    @media (max-width: 576px) {
      @content;
    }
  } @else if $point ==xs {
    @media (max-width: 370px) {
      @content;
    }
  } @else if $point ==xl {
    @media (max-width: 1200px) {
      @content;
    }
  } @else if $point ==xxl {
    @media (max-width: 1400px) {
      @content;
    }
  }
}

@import "../../../global.css";

.carusel_item {
  border-radius: 25px;
  margin: 25px auto;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  position: relative;
  width: 100%;
  height: 500px;
  min-width: 500px;

  @include breakpoint(xxl) {
    width: 100%;
  }
}

.title {
  font-size: 75px;
  letter-spacing: -2px;
  font-weight: bold;
  color: var(--black);
  height: 50%;
  backdrop-filter: blur(1px);
  padding: 25px;
  border-radius: 25px;
  background: linear-gradient(0deg, #ffffff00 0%, var(--violet_light) 100%);
}

.background {
  width: 100%;
  height: 500px;
  min-width: 500px;
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: 75%;
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
}
