@import "../../../global.css";
.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  min-height: 100vh;
  height: 100%;
  margin: 0;
  width: calc(100vw - 5px);
  margin-bottom: 100px;
}
@media (max-width: 1400px) {
  .content {
    flex-direction: column;
  }
}
@media (max-width: 768px) {
  .content {
    height: auto;
  }
}
@media (max-width: 576px) {
  .content {
    border-radius: 10px;
    width: 100vw;
  }
}

.left {
  width: 50vw;
  border-right: medium solid white;
  padding: 15px 75px;
  height: 100%;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: center;
}
@media (max-width: 1400px) {
  .left {
    width: 90vw;
    min-height: auto;
    margin-top: 75px;
    padding: 15px;
    border-right: 0;
    align-items: start;
    border-bottom: medium solid white;
  }
}
@media (max-width: 992px) {
  .left {
    width: calc(100vw - 5px);
  }
}
@media (max-width: 576px) {
  .left {
    margin-top: 75px;
  }
}

.right {
  width: 50vw;
  height: 100vh;
  padding: 25px;
  padding-right: 0;
  padding-bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 1400px) {
  .right {
    width: 90vw;
    height: 100%;
  }
}

.comment {
  font-style: italic;
  font-size: 30px;
  border-left: medium solid rgba(173, 173, 173, 0.3);
  font-family: "Literata", serif;
  line-height: 95%;
  padding: 15px 30px;
}

.author {
  margin-top: 50px;
  margin-left: 50px;
  font-weight: bold;
  font-size: 20px;
}

.direction {
  margin-top: 100px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.306);
}

.description {
  margin-top: 25px;
}

.header {
  font-size: 30px;
  letter-spacing: -1px;
  font-weight: bold;
  margin: 10px 0;
}

.info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 50px;
  margin-bottom: 25px;
}
@media (max-width: 576px) {
  .info {
    margin-top: 25px;
  }
}
.info div {
  font-size: 30px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  letter-spacing: -1px;
  font-weight: bold;
  padding: 5px 15px;
  border-bottom: medium solid rgba(255, 255, 255, 0.246);
}

.image {
  height: 100%;
  background-image: url(../../../../public/img/fon.png);
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: contain;
}
@media (max-width: 1400px) {
  .image {
    background-position: center center;
    background-size: cover;
    border-radius: 25px;
    min-height: 50vh;
    margin-bottom: 50px;
  }
}
@media (max-width: 768px) {
  .image {
    background-size: contain;
    height: 50vh;
  }
}/*# sourceMappingURL=styles.module.css.map */